import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "BeerList",
    // route level code-splitting
    // this generates a separate chunk (BeerList.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "BeerList" */ "@/views/BeerListView"),
  },
  {
    path: "/beer-detail",
    name: "BeerDetail",
    component: () =>
      import(/* webpackChunkName: "BeerDetail" */ "@/views/BeerDetailView"),
  },
  {
    path: "/beer-cart",
    name: "BeerCart",
    component: () =>
      import(/* webpackChunkName: "BeerCart" */ "@/views/BeerCartView"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
